import apiFetch from "@wordpress/api-fetch"
import {Button,PanelBody,PanelRow} from "@wordpress/components"
import { InnerBlocks, InspectorControls,MediaUpload,MediaUploadCheck } from "@wordpress/block-editor"
import { registerBlockType } from "@wordpress/blocks"
import {useEffect} from "@wordpress/element" 

registerBlockType("fitnessblocktheme/fitnessbanner",{
    title:"Fitness Banner",
    supports:{
        align:["full"]
    },
    attributes:{
        align:{type:"string",default:"full"},
        imgID:{type:"number"},
        imgURL:{type:"string",default:fitnessbanner.fallbackImage}
    },
    edit:EditComponent,
    save:SaveComponent
})
function EditComponent(props){
    useEffect(
        function(){
            if(props.attributes.imgID){
                async function go(){
                    const response=await apiFetch({
                        path:`/wp/v2/media/${props.attributes.imgID}`,
                        method:"GET"
                    })
                    props.setAttributes({imgURL:response.media_details.sizes.full.source_url})
                }
                go()
            }
    },[props.attributes.imgID])

    function onFileSelect(x){
        props.setAttributes({imgID:x.id})
        console.log(imgID);
    }

    return (
        <>
            <InspectorControls>
                <PanelBody title="Background" initialOpen={true}>
                    <MediaUploadCheck>
                        <MediaUpload 
                            onSelect={onFileSelect} 
                            value={props.attributes.imgID} 
                            render={({open})=>{
                                return <Button onClick={open}>Choose Image</Button>
                            }}
                        />
                    </MediaUploadCheck>
                </PanelBody>
            </InspectorControls>
            <div className="slide-1 slider_item" style={{backgroundImage:`url('${props.attributes.imgURL}')`,backgroundRepeat: `no-repeat`,backgroundSize:`cover`}} >
                <div className="container" style={{position: `relative`}}> 
                    <div className="slide-text-area align-items-center text-white border-1 border-white">
                        <div className="banner-text">
                            <div className="container p-0">
                                <div className="row">
                                    <div className="col-xxl-6 col-xl-7 col-12"> 
                                        <InnerBlocks allowedBlocks={['core/paragraph','fitnessblocktheme/fitnessheading','fitnessblocktheme/fitnessbutton']}/>
                                         <div className="position-relative banner-arrow">
                                            <i className="fa-solid fa-arrow-left-long me-3 arrow-1"></i>
                                            <i className="fa-solid fa-arrow-right-long me-3 arrow-2"></i>
                                        </div>   
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
}
function SaveComponent(){
    return <InnerBlocks.Content/>
}